export default {
	data() {
		return {
			holidayList: [], // 卡券节日列表
		};
	},
	mounted(){
		this.memberId = JSON.parse(uni.getStorageSync('userInfo')).id
		this.getHolidayList()
	},
	methods: {
		// 获取所有卡券节日列表
		getHolidayList(){
			this.$Request.get(`/appCardController.do?getHolidayList&memberId=${this.memberId}`)
			.then(res => {
				if(res.code == 0){
					this.holidayList = res.data.holidayList
				}else{
					uni.showToast({
						title: res.info,
						icon: 'none'
					})
				}
			})
		},
		chooseHoliday(id){
			this.$Request.get(`/appCardController.do?getHolidayCard&memberId=${this.memberId}&holidayId=${id}`)
			.then(res => {
				if(res.code == 0){
					this.getHolidayList()
				}else{
					uni.showToast({
						title: res.info,
						icon: 'none'
					})
				}
			})
		}
	}
}
